/*
  A stylesheet customizing app (custom components)
*/

html, body {
    margin:0; padding:0; border:0;
}

table.htCore .star {
    color: #fcb515;
}

table.htCore tr.odd td {
    background: #fafbff;
}

table.htCore td .progressBar {
    background: #37bc6c;
    height: 10px;
}

table.htCore tr.selected td {
    background: #edf3fd;
}

/*
  A stylesheet customizing Handsontable style
*/

.handsontable {
    font-size: 13px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Helvetica Neue", Arial, sans-serif;
    font-weight: 400;
}

.collapsibleIndicator {
    text-align: center;
}


.header {
    height: 50px;
    background-color: #dc002d;
    background-image: url("rugr_logonl_wit_rgb_small.png");
    background-repeat: no-repeat;
    background-size: 150px auto;
    background-position: 20px 10px;
    text-align: right;
    color: white;
    font-size: 20pt;
    padding: 10px 20px 0 0;
}

.filters {
    height: 70px;
    padding: 20px 0 0 20px;
}

.MuiPaper-elevation {z-index: 10001; }

.leaflet-control-layers.leaflet-control { display: none; }

.stats-popup {
    background: rgba(255,255,255,0.95);
    border-radius: 0;
    border: 0;
}

.stats-popup .leaflet-popup-tip-container { display: none; }
.stats-popup .leaflet-popup-close-button { display: none; }
.stats-popup .leaflet-popup-content-wrapper { border-radius: 0; box-shadow: none; background: none; }