.stats {
  position: absolute;
  z-index: 999;
  left: 50px;
  bottom: 10px;
  padding: 10px;
  font-size:16px;
  line-height: 2em;
  background: rgba(255,255,255,0.9);
}

.stats tr:first-child th {
  text-align: right;
  border-bottom: 1px solid #ccc;
}

.stats tr:not(:first-child) th {
  text-align: right;
  padding-right: 20px;
}
.stats td {
  text-align: left;
}