.legend {
  position: absolute;
  z-index: 999;
  left: 50px;
  top: 160px;
  padding: 10px;
  font-size:12px;
  letter-spacing:2px;
  background: rgba(255,255,255,0.9);
  border-radius:2px;
  /*width: 200px;*/
}
.legend > div {
  color: black;
  font-family: Georgia;
  position: relative;
  display: flex;
  height: 20px;
  padding: 0 10px;
}

.legend > div > span {
  display: inline-block; width: 50px; text-align: right;
}

.legend > div::before {
  content: "";
  display: inline-block;

  background-color: var(--color);
  left: 0;
  top: 0;
  width: 15px;
  height: 15px;
  border: 1px black solid;
  margin-right: 10px;
}
