@keyframes slide-in {
  0%   { transform: translateX(calc(100vw - 50px)); }
  100% { transform: translateX(calc(850px)); }
}

@keyframes slide-out {
  0%   { transform: translateX(calc(850px)); }
  100% { transform: translateX(calc(100vw - 50px)); }
}


.overlay.collapsed { animation: slide-out 0.2s forwards; }
.overlay.expanded { animation: slide-in 0.2s forwards; }

.overlay {
  display: flex; flex-direction: row;
  padding: 0; border: 0; margin: 0;
  position: fixed;
  height: calc(100vh - 150px);
  width: calc(100vw - 800px);
  top: 150px;
  z-index: 1000;
  background-color: white;
}

.overlay > .content {
  width: calc(100vw - 880px);
}

.overlay > .control {
  width: 20px; height: 100%; padding: 15px;
  font-size: 20pt;
}